import React from 'react'
import styled from 'styled-components';
import { Row, Col, Icon } from 'antd';

const Icone = styled.div`
position: fixed;
z-index: 5;
top: 0;
right: 0;
font-size: 3em;
line-height: 0;
padding-right:0.5%;
div {
    padding: 10% 0%;
    :hover {
    .anticon svg {
    fill: ${({ theme }) => theme.colors.lightblue};
    }
}
}
.anticon svg {
    fill: ${({ theme }) => theme.colors.purple};
}
`


export function Icons(props) {
    return (
        <Icone>
            <div><Icon type={props.social} /></div>
            <div><Icon type={props.social1} /></div>
            <div><Icon type={props.social2} /></div>
        </Icone>
    )
}
